.nav {
  text-decoration: none;
  color: #000;
  text-align: center;
  width: 100%;
  display: inline-block;
}

header {
  flex-direction: column;
  align-items: center;
  text-align: center;
  p {
    margin: 5px 0;
  }
}

.poster {
  margin: 0 -40px;
}

.ratings {
  order: 1;
  &-container {
    width: 100%;
    max-width: 600px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &-score {
    border: 1px solid rgba(0, 0, 0, 0.5);
    border-radius: 50%;
    padding: 10px;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    background-color: rgba(40, 60, 50, 0.05);
  }
}
